import React, { Component, Fragment } from "react";
import axios from "./axios";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";

class Downloads extends Component {
  state = {
    data: [],
    lineChartData: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    //const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const url =
      "https://api.wordpress.org/stats/plugin/1.0/downloads.php?slug=" +
      slug +
      "&limit=30";
    axios.get(url).then((res) => {
      this.setState({ data: res.data });

      const nd = this.state.data;
      //console.log(nd);
      let arr = [];
      let count = 0;
      for (let [key, value] of Object.entries(nd)) {
        const d = new Date(key);
        //const ye = new Intl.DateTimeFormat("en", { year: "2-digit" }).format(d);
        const mo = new Intl.DateTimeFormat("en", { month: "short" }).format(d);
        const da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(d);

        var dateFormatted = `${da} ${mo}`;
        // eslint-disable-next-line no-useless-concat
        count = count + 1;
        arr[count] = {
          Date: dateFormatted,
          Download: value,
        };
        var filterArr = arr.filter((n) => n); // Remove empty data
      }
      this.setState({ lineChartData: filterArr });
    });
  }

  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    var lineData = this.state.lineChartData;
    if (!lineData) return <div className="loading"></div>;
    //============================
    return (
      <Fragment>
        <LineChart width={500} height={300} fontSize={12} data={lineData}>
          <CartesianGrid strokeDasharray="1 1" />
          <XAxis dataKey="Date" />
          <CartesianGrid strokeDasharray="1 1" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Line
            type="monotone"
            dataKey="Download"
            stroke="#82ca9d"
            activeDot={{ r: 8 }}
          />
        </LineChart>
      </Fragment>
    );
  }
}
export default Downloads;
