import React, { Component } from "react";
import axios from "./axios";
import { PieChart, Pie, ResponsiveContainer, Cell, Tooltip } from "recharts";

class ActiveVersion extends Component {
  state = {
    data: [],
  };

  componentDidMount() {
    var slug = localStorage.getItem("slug");
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const url = "https://api.wordpress.org/stats/plugin/1.0/?slug=" + slug;
    axios.get(proxyurl + url).then((res) => {
      this.setState({ data: res.data });
      const d = this.state.data;
      let arr = [];
      let count = 0;
      for (let [key, value] of Object.entries(d)) {
        count = count + 1;
        arr[count] = {
          name: "v " + key,
          value: value,
        };
        var filterArr = arr.filter((n) => n); // Remove empty data
      }
      this.setState({ PieChartData: filterArr });
    });
  }

  render() {
    const d = this.state.data;
    const PieData = this.state.PieChartData;

    if (!d) return <div className="loading"></div>;
    if (!PieData) return <div className="loading"></div>;
    //console.log(PieData);

    const data = PieData;
    const COLORS = [
      "#176BAD",
      "#19AADE",
      "#EA7369",
      "#1AC9E6",
      "#1DE4BD",
      "#C02323",
    ];

    const RADIAN = Math.PI / 180;
    const renderCustomizedLabel = ({
      cx,
      cy,
      midAngle,
      innerRadius,
      outerRadius,
      percent,
      index,
    }) => {
      const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
      const x = cx + radius * Math.cos(-midAngle * RADIAN);
      const y = cy + radius * Math.sin(-midAngle * RADIAN);

      return (
        <text
          x={x}
          y={y}
          fill="white"
          textAnchor={x > cx ? "start" : "end"}
          dominantBaseline="central"
        >
          {`${(percent * 100).toFixed(2)}%`}
        </text>
      );
    };

    //console.log(data);
    return (
      <div style={{ width: "100%", height: 300 }}>
        <ResponsiveContainer>
          <PieChart>
            <Pie
              data={data}
              labelLine={false}
              label={renderCustomizedLabel}
              fill="#8884d8"
              dataKey="value"
              position="top"
            >
              {data.map((entry, index) => (
                <Cell fill={COLORS[index % COLORS.length]} key={index} />
              ))}
            </Pie>
            <Tooltip />
          </PieChart>
        </ResponsiveContainer>
      </div>
    );
  }
}
export default ActiveVersion;
