import React, { Component, Fragment } from "react";

import { faDownload, faStar, faCheck } from "@fortawesome/free-solid-svg-icons";

import DownloadsHistory from "./DownloadsHistory";
import ActiveVersion from "./active-version";
import Downloads from "./download";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Widget extends Component {
  state = {
    local: [],
  };
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        dToday: localStorage.getItem("downloadToday"),
        dYesterday: localStorage.getItem("downloadYesterday"),
      }); //set local state
    }, 2000);
  }
  render() {
    if (!this.state.dToday) return <div className="loading"></div>;
    const downloadToday = this.state.dToday;
    const range = this.state.dToday - this.state.dYesterday;
    return (
      <Fragment>
        <div className="row mt-5">
          <div className="col-lg-3 col-md-3">
            <div className="widget text-center">
              <div className="widget-heading clearfix">
                <div className="pull-left">Download Today</div>
              </div>
              <div className="widget-body clearfix">
                <div className="pull-left">
                  <FontAwesomeIcon icon={faDownload} color="#28a745" />
                </div>
                <div className="pull-right number">
                  {downloadToday}
                  <div style={{ fontSize: 16 }}>
                    <span
                      className={
                        range >= 0
                          ? "badge badge-success"
                          : "badge badge-danger"
                      }
                    >
                      {range} {""}
                      vs yesterday
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-3 col-md-3">
            <div className="widget text-center">
              <div className="widget-heading clearfix">
                <div className="pull-left">Active Install</div>
              </div>
              <div className="widget-body clearfix">
                <div className="pull-left">
                  <FontAwesomeIcon icon={faCheck} color="#28a745" />
                </div>
                <div className="pull-right number">
                  {this.props.active_installs}+
                  <div style={{ fontSize: 13 }}>
                    but less than {this.props.probable_active_install}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-3 col-md-3">
            <div className="widget text-center">
              <div className="widget-heading clearfix">
                <div className="pull-left">Average Ratings</div>
              </div>
              <div className="widget-body clearfix">
                <div className="pull-left">
                  <FontAwesomeIcon icon={faStar} color="#28a745" />
                </div>
                <div className="pull-right number">{this.props.AvgRating}</div>

                <div style={{ fontSize: 13 }}>
                  based on {this.props.number_of_rating} reviews
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-3 col-md-3">
            <div className="widget text-center">
              <div className="widget-heading clearfix">
                <div className="pull-left">Download History</div>
              </div>
              <div className="widget-body clearfix pt-0">
                <DownloadsHistory />
              </div>
            </div>
          </div>
          <div className="col-lg-6 col-md-6">
            <div className="widget-body text-center">
              <div className="clearfix">
                <div className="pull-left">Active version</div>
              </div>
              <div className="clearfix pt-0">
                <ActiveVersion />
              </div>
            </div>
          </div>
          <div className="col-lg-6 col-md-6">
            <div className="widget-body text-center">
              <div className="clearfix">
                <div className="pull-left">Downloads last 30 days</div>
              </div>
              <div className="clearfix pt-0">
                <Downloads />
              </div>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}
export default Widget;
